// @flow

import React, { Component } from 'react';
import update from 'immutability-helper';
import Header from './components/Header';
import Release from './components/Release';
import recordPlaceholderImage from './record.png';
import './App.css';

// dummy Discogs key
const discogs = 'qYDmZtPoZAYMXBYnCScAksHPBcBhwazZncNTleON';

type State = {
  releases: {
    byId: {
      [string]: ReleaseType
    },
    allIds: Array<string>
  }
};

class App extends Component<any, State> {
  constructor(props: any) {
    super(props);
    this.state = {
      releases: {
        byId: {},
        allIds: []
      }
    };
  }

  componentDidMount() {
    fetch(
      `https://api.discogs.com/users/phacks-api/collection/folders/0/releases?token=${discogs}`
    )
      .then(response => response.json())
      .then(data => {
        let newState = {
          releases: {
            byId: {},
            allIds: []
          }
        };
        const promises = data.releases.map((release, index) =>
          fetch(
            `https://api.discogs.com/releases/${release.id}?token=${discogs}`
          )
            .then(response => response.json())
            .then(data => {
              newState = {
                releases: {
                  byId: {
                    ...newState.releases.byId,
                    [data.id]: {
                      artist: data.artists[0].name,
                      imageUrl: data.images
                        ? data.images.find(image => image.type === 'primary')
                            .uri
                        : recordPlaceholderImage,
                      title: data.title,
                      tracklist: data.tracklist.map(track => ({
                        duration: track.duration,
                        title: track.title,
                        position: track.position
                      })),
                      year: data.year
                    }
                  },
                  allIds: update(newState.releases.allIds, { $push: [data.id] })
                }
              };
            })
        );
        return Promise.all(promises).then(() => this.setState(newState));
      });
  }

  render() {
    return (
      <div className="App">
        <Header />
        <div id="columns">
          {this.state.releases.allIds.map(releaseId => {
            const release = this.state.releases.byId[releaseId];
            return <Release key={releaseId} release={release} />;
          })}
        </div>
      </div>
    );
  }
}

export default App;



